import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import Vuetify from 'vuetify'
import BuyModalComponent from '@/components/Shared/BuyModal'
import * as fb from 'firebase'
import 'vuetify/dist/vuetify.min.css'

Vue.use(Vuetify)
Vue.component('app-buy-modal', BuyModalComponent)
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>',
  created () {
    fb.initializeApp({
      apiKey: 'AIzaSyAaT7U7W2k57Kr-tKOvGQ0KiyxcRcB0qgY',
      authDomain: 'itc-ads-d2d4c.firebaseapp.com',
      databaseURL: 'https://itc-ads-d2d4c.firebaseio.com',
      projectId: 'itc-ads-d2d4c',
      storageBucket: 'itc-ads-d2d4c.appspot.com',
      messagingSenderId: '140155395771',
      appId: '1:140155395771:web:eee46889c0e937b187139e',
      measurementId: 'G-EFWWE1G2P3'
    })

    fb.auth().onAuthStateChanged(user => {
      if (user) {
        this.$store.dispatch('autoLoginUser', user)
      }
    })

    this.$store.dispatch('fetchAds')
  }
})



